import { request,getToken,Token } from '../../request.js'
export default {
	data() {
		return {
			baseURL:this.$api,
			isLogin:true,
			user_name:'未登陆',
			avatar:'/static/images/user-default.jpeg',
			phone:'',
			ordernum:{
				0:0,
				1:0,
				2:0
			},
			area:'',
			$api: ''
		};
	},
	onShow() {
		this.getUserInfo();
	},
	onLoad() {
		this.$api = this.$api
	},
	methods:{
		// 跳转到reset修改密码页
		reset() {
			uni.navigateTo({
				url: '/pages/components/reset/reset'
			})
		},
		//获取信息
		getUserInfo() {
			let that = this;
			request({
					url:"index.php?s=/wap/member/Api_member_index",
					method:"POST",
				}).then(function(res){
					// console.dir(res)
					that.user_name = res.data.user_name;
					that.avatar = that.baseURL + res.data.member_img;
					that.phone = res.data.phone;
					
					let address = res.data.address;
					that.area = address.province + " " + address.city + " " + address.district;
				});
		},
		//上传图片
		changeImg() {
			let that = this;
		        uni.chooseImage({
		            success: (chooseImageRes) => {
		                const tempFilePaths = chooseImageRes.tempFilePaths;
		                this.changeimg = tempFilePaths[0] 
		                uni.uploadFile({
		                    url: this.$api +'index.php?s=/wap/upload/uploadFile', //仅为示例，非真实的接口地址
		                    filePath: tempFilePaths[0],
		                    name: 'file_upload',
		                    formData: {
		                        file_path:'upload/avator/',
		                    },
												
		                    success: (res) => {
		                        var res = JSON.parse(res.data)
		                        if (res.code == 1) {
															that.avatar = this.$api + res.data;
															console.log(that.avatar);
															that.modifyFace(res.data);
															uni.showToast({
																	title:res.message,
																	icon:'none',
																	duration:2000
															})
		                        }
														else {
															uni.showToast({
																	title:res.message,
																	icon:'none',
																	duration:2000
															})
		                        }
		                    }
		                });
		            }
		        });
		 },
		 //保存图片路径到数据库
		modifyFace(user_headimg){
			console.log(user_headimg);
			request({
				url: "index.php?s=/wap/member/apimodifyFace",
				method: "POST",
				data: {
					user_headimg: user_headimg,
				}
			}).then(res => {
				console.log(res);
				if(res.data.code == 1){
					console.dir('保存成功');	
					
				}
				else {
					console.dir('保存失败');
				}
			})
		},
		// 跳转到修改昵称页
		changeName() {
			uni.navigateTo({
				url: '/pages/components/change-username/change-username'
			})
		},
		// 安全退出
		signOut() {
			uni.showModal({
				content: '确认安全退出吗？',
				success: res => {
					if(res.confirm) {
						uni.removeStorage({
							key:'token',
							success: () => {
								this.isLogin = false;
								uni.switchTab({
									url: '/pages/index/index'
								})
							}
						})
					}
				}
			})
		},
		
		
		 
	},
	
}

